#episode-selector {
    position: relative;
    padding-top: 1em;
    font-size: 0.6rem;
}

.episode-selector-header {
    margin-top: 48px;
    margin-bottom: 20px;
}

.episode-titlecard {
    display:flex;
    align-items: center;
    min-height: 8em;
    padding: 1rem;
    border-top: 1px solid #3f3f3f;
    border-bottom: 1px solid #3f3f3f;
    border-radius: 3px;
    font-family: Helvetica, Arial, sans-serif;
}

.titlecard-index {
    display: flex;
    justify-content: center;
    font-size: 1.4rem;
    flex: 0 0 8%;
}

.titlecard-img {
    width: 157px;
    height: 78px;
    object-fit: cover;
    background-color: #bfbfbf;
    flex: 0 0 18.5%;
}

.titlecard-img > img {
    width: 100%;
    height: 100%;
}

.titlecard-text {
    padding: 0.9rem;
    flex: 0 0 66%;
}

.titlecard-title {
    font-size: 0.85rem;
    font-weight: bold;
    padding-bottom: 0.55rem;
    display: flex;
    justify-content: space-between;
}

.titlecard-description {
    font-size: 0.84rem;
    font-weight: bold;
    color: #bfbfbf;
    padding: 0;
    margin: 0;
}