#footer {
    color: white;
    margin: 20px auto 0;
    max-width: 980px;
    padding: 0 4%;
}

#social-links{

}

#service-footer-links {
    display: flex;
    flex-wrap: wrap;
    color: grey;
    margin: 0;
    padding: 0;
    margin-bottom: 0.75rem;
}

.service-link {
    list-style-type: none;
    flex-basis: 25%;
    margin-bottom: 1rem;
    font-size: 0.7rem;
}

.service-link:hover {
    text-decoration: underline;
    cursor: pointer;
}

#service-code-btn {
    border: 1px solid grey;
    background: none;
    color: grey;
    margin-bottom: 0.6rem;
}

#service-code-btn:hover {
    color: white;
    cursor: pointer;
}

#copyright {
    color: grey;
    font-size: 0.6rem;
}