.content-item {
	display: inline-block;
	position: relative;
	height: inherit;
	/* width: 16.7%; */
	margin: 0 2px;
	border: 1px solid transparent;
	transition: transform 0.1s ease-in-out;
}

.content-item + .expanded {
	transform: scale(1.5) translateY(-35%);
}

.current-row-first.expanded  {
	transform: scale(1.5) translate(16.5%,-35%);
}

.current-row-last.expanded {
	transform: scale(1.5) translate(-16.5%,-35%);
}

.content-img {
	width: 100%;
	height: 100%;
	border-radius: 2% 2% 0 0;
	object-fit: cover;
}

.previous-row, .next-row {
    z-index: 0;
}

#selected-item {
    z-index: 2;
}