.expand-btn {
    background: #1f1f1fd5;
    border-radius: 50%;
    width: 2.5rem;
    height: 2.5rem;
    border: 1px solid #949494;
    color: white;
    position: absolute;
    left: 0;
    right: 0;
    bottom: -1.25rem;
    margin: 0 auto;
    text-align: center;
}

.expand-btn:hover {
    background: #4b4b4b;
    border: 1px solid white;

}

.divider {
    position: absolute;
    padding: 0.2px;
    border-bottom: 1px solid #535353;
    height: 100px;
    bottom: 0;
    left: 0;
    right: 0;
}

.collapsed {
    background-image: linear-gradient(to top,#181818 0,rgba(24,24,24,.7) 20%,rgba(24,24,24,.4) 30%,transparent 50%);
}