.feature-strip {
	margin: 3vw 0;
    padding: 0 3vw;
    overflow-x: clip;
}

.feature-title {
	margin: 0 0 1rem 0;
	color: white;
	font-size: calc(0.75vh + 0.75vw + 0.4vmin);
}

.feature-content {
    white-space: nowrap;
    height: 9vw;
    display: grid;
    grid-auto-flow: column;
    grid-template-columns: repeat(6,16.6666666667%);
    grid-auto-columns: 16.6666666667%;
}

.feature-area:hover {
    position: relative;
    z-index: 2;
}

.feature-area {
    position: relative;
    white-space: nowrap;
    height: 9vw;
}

.anim {
    transition: transform 0.4s;
}

.prev-btn {
    position: absolute;
    top: 0;
    bottom: 0;
    right: 100%;
    width: 4%;
    background: #ffffff24;
    color: transparent;
    font-size: 2rem;
    border: none;
    font-weight: bold;
    z-index: 1;
}

.next-btn {
    position: absolute;
    top: 0;
    bottom: 0;
    left: 100%;
    width: 4%;
    background: #ffffff24;
    color: transparent;
    font-size: 2rem;
    border: none;
    font-weight: bold;
    z-index: 1;
}

.next-btn:hover, .prev-btn:hover {
    color: white;
    background: #0000004d;
}