#item-showcase {
	width: 100%;
	height: 40vw;
	max-height: 100vh;
}

#item-showcase > img {
	width: inherit;
	height: 54vw;
	max-height: inherit;
	object-fit: cover;
    object-position: center top;
}

#img-bottom-mask {
	background: linear-gradient(to bottom,rgba(20,20,20,0) 0,rgba(20,20,20,.15) 15%,rgba(20,20,20,.35) 29%,rgba(20,20,20,.58) 44%,#141414 68%,#141414 100%);
	position: absolute;
	bottom: 0;
	width: 100%;
	height: 14.7vw;
}

#showcase-preview  {
    width: 100%;
    position: relative;
    z-index: -1;
}

#showcase-preview > img{
    width: 100%;
}

#showcase-billboard {
	position: absolute;
	top:75px;
	left:0;
	width: 50%;
	height: 30vw;
	max-height: inherit;
    display:flex;
    flex-direction: column;
    justify-content: flex-end;
    margin-left: 3vw;
}

#showcase-title {
	color: white;
	font-size: 3.5vw;
}

#showcase-play-btn, #showcase-info-btn {
	font-size: calc(0.75vh + 0.75vw + 0.4vmin);
	font-weight: 600;
	padding: 1.5% 4%;
	margin-right: 1.2rem;
	border: none;
	border-radius: 5%;
	text-align: center;
}

#showcase-info-btn {
	background-color: rgba(100, 100, 100, 0.7);
	color: white;
}

#showcase-btns > button > svg {
	width: 1.2rem;
	vertical-align: middle;
	margin-right: 1rem;
}

#showcase-btns > #showcase-info-btn > svg {
	width: 1.6rem;
	fill: white;
}

#showcase-play-btn:hover {
	background-color: rgba(250, 250, 250, 0.7);
}

#showcase-info-btn:hover {
	background-color: rgba(70, 70, 70, 0.4);
}