.darken-background {
    position: fixed;
    background: #1f1f1f9f;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    z-index: 5;
    overflow: auto;
    overscroll-behavior: contain;
}

.fixed {
    position: fixed;
}

#preview-modal {
    position: relative;
    top: 1.7em;
    width: 900px;
    min-height: 600px;
    background: #181818;
    z-index: 5;
    margin: auto;
    color: white;
    border: 1px solid black;
}

#preview {
    position: relative;
}

#preview-img {
    width: 100%;
}

#preview-details{
    position: absolute;
    bottom: 4rem;
    left: 3rem;
    right: 0;
    width: 42%;
}

#preview-text{
    color: white;
    text-align: center;
}

#gradient {
    position: absolute;
    top: 0;
    width: 100%;
    height: 100%;
    background: linear-gradient(to top,#181818,transparent 50%)
}

.detail-btns{
    display: flex;
    flex-direction: row;
}

.detail-btns > .info-btn {
    width: 2.25rem;
    height: 2.25rem;
}

.info-btn.square{
    fill:black;
    background: white;
    padding: 0.5rem 1.5rem;
    border-radius: 5px;
    border: none;
    font-size: 1rem;
    width: auto;
    height: min-content;
}

.info-btn.square > div {
    width: 2rem;
}

.info-btn.square:hover{
    filter: brightness(80%);
}

#expanded-info {
    padding: 0 4.9% 5.4% 4.8%;
}

#expanded-info > * {
    font-size: 1rem;
}

#expanded-metadata {
    margin: 0.8em 0;
    height: 2.1rem;
    display: flex;
    align-items: center;
}

#expanded-compatibility, #expanded-seasons, #expanded-release-date {
	display: inline-block;
	margin: 0 0.2rem;
}

#expanded-compatibility
{
    color: green
}

#expanded-description { 
    font-family: Helvetica, Arial, sans-serif;
    font-size: 0.95rem;
    font-weight: bold;
    line-height: 24px;
    margin: 1em 0 0.5em 0;
}

.tag-group {
    font-size: 0.72rem;
    color: #7f7f7f;
    margin: 0.6rem 0;
}

.tag-group > span::after {
    content: ': ';
}

.tag-group > a {
    color: white;
    font-weight: bold;
}

#details {
    display: grid;
    grid-template-columns: 2.1fr 1fr;
    column-gap: 1.9em;
}

#details-right{
    display: flex;
    flex-direction: column;
}

#preview-close-btn {
    position: absolute;
    top: 1rem;
    right: 1rem;
    width: 2rem;
    height: 2rem;
    border: none;
    background: #181818;
    transform: rotate(45deg);
}

#preview-close-btn > svg {
    width: 1.5rem;
    height: 1.5rem;
}