#more-like-this {
    position: relative;
}

#suggested-shows{
    display: flex;
    flex-wrap: wrap;
    gap: 1.1rem;
    padding-bottom: 5.5rem 
}

.suggested-show {
    background: #1f1f1f;
    width: 258px;
    height: 341px;
    font-size: 0.8rem;
    border-radius: 5px;
}

.suggested-show > img {
    width: 100%;
    object-fit: cover;
    border-radius: 5px 5px 0 0;
}

.suggested-metadata {
    font-size: 0.75rem;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0.6rem 1rem 1rem 1rem;
}

.metadata-left {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    min-height: 3.67rem;
}
.metadata-left > *{
    padding: 0;
    margin: 0;
}

.metadata-add-btn {
    width: 2.5rem;
    height: 2.5rem;
    background: none;
    border-radius: 50%;
    border: 1px solid #949494;
    display: flex;
    justify-content: center;
    align-items: center;
}

.metadata-add-btn > svg {
    fill: white;
    width: inherit;
    height: inherit;
}

.suggested-desc {
    padding: 0 0.9rem 0.9rem 0.9rem;
    margin: 0;
    font-size: 0.799rem
}

.clip {
    height: 515px;
    overflow: clip;
}